import React, { Component } from "react";

import "./apps.css";
import Day from "../apps/components/Day";

// Images Import
import Search from "../img/search.svg";
import weatherIcon from "./img/sun.png";
import chevron from "./img/chevron.png";
import wind from "./img/wind.png";
import humidity from "./img/humidity.png";
import temp from "./img/temp.png";

// Stores includes environment variables
require("dotenv").config();

class Weather extends Component {
  constructor() {
    super();
    this.state = {
      menu: "closed",
      hideSearch: true,
      menuState: "",
      searchValue: "",
      placeHolder: "",

      searchVisible: "hidden",
      // Weather Controls

      location: "London",
      temp: "17",
      temp_min: "",
      temp_max: "",
      humidity: "",
      windSpeed: "2mph",
      status: "clear",
      statusDescription: "clear and sunny",
      weatherIcon: "01d",
      data: [],
      days: [],
      day: 0,
      daysDisplay: []
    };

    this.menuControl = this.menuControl.bind(this);
    this.searchControl = this.searchControl.bind(this);
    this.handleChange = this.handleChange.bind(this);
    // change day number to update details
    this.changeDetails = this.changeDetails.bind(this);

    this.newSearch = this.newSearch.bind(this);
  }
  changeDetails(id) {
    this.setState({ day: id });
  }
  componentDidMount() {
    this.setState({ location: "Loading..." });
    fetch(
      "http://api.openweathermap.org/data/2.5/forecast?q=" +
        "Jamaica" +
        "&units=imperial&APPID=" +
        process.env.REACT_APP_API_KEY
    )
      .then(data => data.json())
      .then(data => {
        // Get each Individual Days and store them in this.data array

        // Clears data and days array for new search
        this.setState({ data: [] });
        this.setState({ days: [] });
        // For loop that filters out each day
        for (var i = 0; i < data.list.length; i++) {
          // Catches prev date
          var prev;
          // splits date text return into year month day for comparison
          var dateTxt = data.list[i].dt_txt.split(" ");

          // Push each day to array
          if (dateTxt[0] !== prev) {
            this.state.days.push(data.list[i]);

            prev = dateTxt[0];
          }
        }

        // Clear daysDisplay array
        this.setState({
          daysDisplay: [],
          temp_max: Math.round(data.list[this.state.day].main.temp_max),
          temp_min: Math.round(data.list[this.state.day].main.temp_min)
        });
        let days = this.state.days;
        let weekdays = [
          "Sunday",
          "Monday",
          "Tuesday",
          "Wednesday",
          "Thursday",
          "Friday",
          "Saturday"
        ];
        // Create day cards and pass props
        for (let i = 0; i < days.length; i++) {
          // Store Year Month and Day from the dt_txt return
          let ymd = this.state.days[i].dt_txt.split(" ");
          let date = new Date();
          let icon = "";
          // console.log(ymd[0]);
          icon = days[i].weather[0].icon.charAt(0);
          icon += days[i].weather[0].icon.charAt(1);
          // console.log(icon);
          this.state.daysDisplay.push(
            <Day
              day={ymd[0]}
              tempMin={days[i].main.temp_min}
              temp={days[i].main.temp}
              tempMax={days[i].main.temp_max}
              wind={days[i].wind.speed}
              description={days[i].weather.description}
              icon={icon}
              id={i}
              key={i + days.length}
            />
          );
        }
        // Store day one to data array for base info
        // this.setState({ data: data.list[0] });

        // Set Base Info for App
        // Used this.state.day to make it dynamic, user can swipe through days
        this.setState({
          windSpeed: data.list[this.state.day].wind.speed + "mph",
          location: data.city.name,
          temp: Math.floor(data.list[this.state.day].main.temp),
          temp_max: Math.round(data.list[this.state.day].main.temp_max),
          temp_min: Math.round(data.list[this.state.day].main.temp_min),
          humidity: Math.round(data.list[this.state.day].main.humidity),
          status: data.list[this.state.day].weather[this.state.day].main,
          statusDescription:
            data.list[this.state.day].weather[this.state.day].description,
          weatherIcon:
            "./img/" +
            data.list[this.state.day].weather[this.state.day].icon +
            ".png"
        });

        // console.log(this.state.days);
      });
  }
  newSearch() {
    this.setState({ location: "Loading..." });
    fetch(
      "http://api.openweathermap.org/data/2.5/forecast?q=" +
        this.state.searchValue +
        "&units=imperial&APPID=" +
        process.env.REACT_APP_API_KEY
    )
      .then(data => data.json())
      .then(data => {
        // Get each Individual Days and store them in this.data array

        // Clears data and days array for new search
        this.setState({ data: [] });
        this.setState({ days: [] });
        // For loop that filters out each day
        for (var i = 0; i < data.list.length; i++) {
          // Catches prev date
          var prev;
          // splits date text return into year month day for comparison
          var dateTxt = data.list[i].dt_txt.split(" ");

          // Push each day to array
          if (dateTxt[0] !== prev) {
            this.state.days.push(data.list[i]);

            prev = dateTxt[0];
          }
        }
        // **********************************************
        // ***********Code to display each day**********
        // map each day into days Display array

        // Clear daysDisplay array
        this.setState({
          daysDisplay: [],
          temp_max: Math.round(data.list[this.state.day].main.temp_max),
          temp_min: Math.round(data.list[this.state.day].main.temp_min)
        });
        let days = this.state.days;
        let weekdays = [
          "Sunday",
          "Monday",
          "Tuesday",
          "Wednesday",
          "Thursday",
          "Friday",
          "Saturday"
        ];

        for (let i = 0; i < days.length; i++) {
          // Store Year Month and Day from the dt_txt return
          let ymd = this.state.days[i].dt_txt.split(" ");
          let date = new Date();
          // console.log(ymd[0]);

          this.state.daysDisplay.push(
            <Day
              day={ymd[0]}
              tempMin={days[i].main.temp_min}
              temp={days[i].main.temp}
              tempMax={days[i].main.temp_max}
              wind={days[i].wind.speed}
              description={days[i].weather.description}
              icon={days[i].weather[0].icon}
            />
          );
          // console.log(date.getDay(ymd[0]));
        }

        // ***************************************************
        // Store day one to data array for base info
        this.setState({ data: data.list[0] });

        // Display Days in console
        // for (var i = 0; i < this.state.days.length; i++) {
        //   // console.log(this.state.days[i].dt_txt);
        // }

        // Text Output
        // console.log(this.state.days);

        // Set Base Info for App
        // Used this.state.day to make it dynamic, user can swipe through days
        this.setState({
          windSpeed: data.list[this.state.day].wind.speed + "mph",
          location: data.city.name,
          temp: Math.floor(data.list[this.state.day].main.temp),
          status: data.list[this.state.day].weather[this.state.day].main,
          statusDescription:
            data.list[this.state.day].weather[this.state.day].description,
          weatherIcon:
            "./img/" +
            data.list[this.state.day].weather[this.state.day].icon +
            ".png"
        });
      });
  }
  searchControl() {
    if (this.state.hideSearch === true) {
      // Animate showing search bar
      this.setState({
        hideSearch: false,
        searchVisible: "visible"
      });
    } else {
      if (this.state.searchValue !== "") {
        this.newSearch();
      } else {
        this.setState({
          hideSearch: true,
          searchVisible: "hidden"
        });
      }
    }
  }
  menuControl() {
    if (this.state.menu === "closed") {
      this.setState({
        menu: "open",
        menuState: "is-active"
      });
    } else {
      this.setState({
        menu: "closed",
        menuState: ""
      });
    }
  }
  handleChange(event) {
    const { name, value } = event.target;
    this.setState({ [name]: value, searchValue: value });
  }

  render() {
    const d = new Date();
    const days = [
      "Sunday",
      "Monday",
      "Tuesday",
      "Wednesday",
      "Thursday",
      "Friday",
      "Saturday"
    ];
    const months = [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec"
    ];
    return (
      <div className="background">
        <nav>
          <button
            onClick={this.menuControl}
            className={"hamburger hamburger--collapse " + this.state.menuState}
            type="button"
          >
            <span class="hamburger-box">
              <span class="hamburger-inner"></span>
            </span>
          </button>
          <h1>Weather</h1>

          <form>
            <label
              style={{ visibility: this.state.searchVisible }}
              htmlFor="search"
            >
              <input
                type="text"
                id="search"
                placeholder={this.state.location}
                value={this.state.searchValue}
                onChange={this.handleChange}
              />
            </label>
          </form>

          <img onClick={this.searchControl} src={Search} alt="" />
        </nav>
        <div className="weatherInfo">
          <div className="weatherPreview">
            <div>
              <img src={weatherIcon} />
              <p>
                <strong>{d.getDate() + " " + months[d.getMonth()]}</strong>
                <br />
                {days[d.getDay()].toUpperCase()}{" "}
              </p>
            </div>

            <h1>
              {Math.round((this.state.temp_max + this.state.temp_min) / 2)}
              <sup>o</sup>
              <span>
                {"/" + this.state.temp_min}
                <sup>o</sup>
              </span>
            </h1>
            <h3>{this.state.location.toUpperCase()}</h3>
          </div>
          <div className="daysPreview">{this.state.daysDisplay}</div>
        </div>

        <div className="weatherDetails">
          <h2>Details</h2>
          <div>
            <div className="categories">
              <h1>
                <span>{/* <img src={wind} /> */}</span>
                {"Status : " + this.state.statusDescription}
              </h1>
            </div>

            <div className="categories">
              <h1>
                <span>
                  <img src={humidity} />
                </span>
                Humididty
              </h1>
              <h1>{this.state.humidity + "%"}</h1>
            </div>

            <div className="categories">
              <h1>
                <span>
                  <img src={wind} />
                </span>
                Wind
              </h1>
              <h1>{this.state.windSpeed}</h1>
            </div>
            <div className="categories">
              <h1>
                <span>
                  <img src={temp} />
                </span>
                Temp-Max
              </h1>
              <h1>
                {this.state.temp_max}
                <sup>o</sup>
              </h1>
            </div>

            {/* <h1>
              More Details{" "}
              <span>
                <img src={chevron} />
              </span>
            </h1> */}
          </div>
        </div>
      </div>
    );
  }
}

export default Weather;
