import React, { Component } from "react";
import "../apps.css";

// Import weather app to call function
class Days extends Component {
  constructor() {
    super();
    this.state = {};

    this.activeDay = this.activeDay.bind(this);
  }

  // Highlights card that is touched
  activeDay(event) {
    const { target } = event;
    let id = target.id;
    // console.log(document.getElementById(id).parentNode.className);

    for (var i = 0; i < 6; i++) {
      if (document.getElementById(id).parentNode.className == "day") {
        document.getElementById(id).parentNode.classList.add("active");
      } else {
        document.getElementById(i).parentNode.classList.remove("active");
      }
    }
  }
  render() {
    // Create on click for css change
    let dayIcon = this.props.icon + "d.png";
    let nightIcon = this.props.icon + "n.png";

    return (
      <div
        // onClick={this.activeDay}
        // className="day"
        className={this.props.id == 0 ? "day active" : "day"}
      >
        {/* day */}
        <p>
          Y-M-D <br />
          {this.props.day}
        </p>
        <br />

        {/* <img
          id={this.props.id}
          src={
            new Date().getHours() < 18
              ? require("../img/" + dayIcon)
              : require("../img/" + nightIcon)
          }
        /> */}
        <br />
        {/* temp */}
        <p>
          {Math.round(this.props.tempMin)}
          <sup>o</sup>
        </p>
      </div>
    );
  }
}

export default Days;
